
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 100px;
}

#input,#language,#submit{
  margin-top: 10px;
  margin-bottom: 10px;
  margin-right: 20px
}

@media screen and (min-width: 992px) {
#translateOutput{
    max-width: 50%;
}
}
@media screen and (max-width: 991px) {
#translateOutput{
    max-width: 100%;
}
}

